.profile {
    width: 100%;
    min-width: 900px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: start;
    padding-top: 40px;
}

@media all and (max-width: 900px) {
    .profile {
        min-width: 0;
        flex-direction: column;
    }
}